import React from 'react'
import HomeOutlinedIcon from '@material-ui/icons/HomeOutlined';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';
import {useState} from 'react'
import CloseIcon from '@material-ui/icons/Close';
import {fire} from './configure'
import { Nav, Navbar } from 'react-bootstrap';

function Header() {
      const firebee=fire

    const [state, setState] = useState(true)
    const state_change=()=>{
      setState(!state)
      
      
    }
      const [user, setuser] = useState(false)

   firebee.auth().onAuthStateChanged((user) => {
     if(user){
       setuser(true)
     
     }else{
       setuser(false)
       
     }
   })
  const signout=()=>{
    firebee.auth().signOut()
  }
 
    return (
        <div >
            <Navbar expand="md" bg='light' className=''>
        <Navbar.Brand>Productin</Navbar.Brand>
     
        <Navbar.Toggle aria-controls='basic-navbar-nav' type='link'  onClick={state_change} >{state ?(<span className='navbar-toggler-icon' id='navbar-toggler'></span>):(<CloseIcon  id='close'   />)}
</Navbar.Toggle>

        <Navbar.Collapse id="basic-navbar-nav">
          <Nav activekey='/' className='m-auto f'>
            <Nav.Item>
              <Nav.Link href='/'><HomeOutlinedIcon/>Home</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link href='/wishlist'><FavoriteBorderIcon/>Wishlist</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link href='/cart'><ShoppingCartOutlinedIcon/>Cart</Nav.Link>
            </Nav.Item>
            
            <Nav.Item>
              
              {!user ? <Nav.Link href='/signin'>Sign In</Nav.Link> : 
               <Nav.Link onClick={signout}>Sign Out</Nav.Link>}
              {/* <H/>
              <K/> */}
            </Nav.Item>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
        </div>
    )
}

export default Header
