import React from 'react'
import {useState} from 'react'
import { Card,CardImg, CardSubtitle, CardTitle } from 'reactstrap'
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import FavoriteIcon from '@material-ui/icons/Favorite';
import { IconButton } from '@material-ui/core';
import Configure, { fire } from './configure'
import {dishess} from './raw_manual'
import './dishes.css'
import firebase from 'firebase/app'

function Dishes() {
    const firebee=fire;
    console.log(fire.auth().currentUser)
  if(fire.auth().currentUser){
    firebee.firestore().collection(fire.auth().currentUser.uid).doc(`cart`).set({
        dishes:[]
    })
    firebee.firestore().collection(fire.auth().currentUser.uid).doc(`wish`).set({
        dishes:[]
    })}
    const Handle=(item,id)=>{  

if(fire.auth().currentUser){
   firebee.firestore().collection(fire.auth().currentUser.uid).doc(`${item}`).update({
            dishes:firebase.firestore.FieldValue.arrayUnion(`${id}`)
        })
}
}
    const v=dishess.map((k)=>{
         
        return(
            
        <Card key={k.id} className='col-11 col-sm-6 col-md-4' >
            <CardImg src={k.image} className='img'/>
            <div className="price">
            <h4 >{k.title}</h4>
            
            <FavoriteIcon className='wish1'  onClick={()=>{Handle('wish',k.id)}} />
           
            <AddShoppingCartIcon className='cart'onClick= {()=>{Handle('cart',k.id)}} />
            <h3>{k.Price}</h3>
            </div>
        </Card>
       
     
        )
    })

    
    
    
     
    return (
        <div className='row gx-0'>
            {v}
            
        </div>
    )
}

export default Dishes