import { Nav, Navbar } from 'react-bootstrap';
import './App.css';
import {useState} from 'react'
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';
import Fruits from './fruits';
import CloseIcon from '@material-ui/icons/Close';
import { Icon, IconButton } from '@material-ui/core';
import $ from 'jquery'
import HomeOutlinedIcon from '@material-ui/icons/HomeOutlined';
import Dishes from './dishes'
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import {Link, BrowserRouter as Router} from 'react-router-dom'
import {fire} from './configure'
import Header from './header';
import {fruitss,dishess} from './raw_manual'
function App() {
  const firebee=fire;


    let j=document.querySelectorAll('.nav-links.nav-link')
  
    j.forEach(element => {
      
      document.getElementById(element.id).addEventListener('click',function(){
        let k=element.id
        j.forEach(element => {
          if(element.id==k){
        document.getElementById(element.id).style.color='red'}
        else{
           document.getElementById(element.id).style.color='blue'
        }
      })})
    });
    
  const nav=(k)=>{
      if(k.target.innerHTML==='Fruits'){
        setfruits(true)
        setrrrr(false);
        setdishes(false)
        setssss(false)
        seteeee(false)
      }
      if(k.target.innerHTML==='Dishes'){
        setdishes(true)
        setssss(false);
        setfruits(false)
        setrrrr(false)
        seteeee(false)
      }
      if(k.target.innerHTML==='ssss'){
        setssss(true)
        setrrrr(false);
        setdishes(false)
        setfruits(false)
        seteeee(false)
      }
      if(k.target.innerHTML==='eeee'){
        seteeee(true)
        setssss(false);
        setdishes(false)
        setrrrr(false)
        setfruits(false)
      }
      if(k.target.innerHTML==='rrrr'){
        setrrrr(true)
        setssss(false);
        setdishes(false)
        setfruits(false)
        seteeee(false)
      }
    }

    const [fruits, setfruits] = useState(true)
    const [dishes, setdishes] = useState(false)
    const [ssss, setssss] = useState(false)
    const [rrrr, setrrrr] = useState(false)
    const [eeee, seteeee] = useState(false)
  


  return (
    <div>
      <div id='nav-2'>
     <Header/>
  
        <Nav justify  id='nav1'>
         
          <Nav.Item onClick={nav}>
        <Nav.Link className='nav-links'id='fruits' >Fruits</Nav.Link>
        </Nav.Item>
        <Nav.Item>
        <Nav.Link className='nav-links' id='dishes' onClick={nav}>Dishes</Nav.Link>
        </Nav.Item>
        <Nav.Item>
        <Nav.Link className='nav-links'id='ssss' onClick={nav}>ssss</Nav.Link>
        </Nav.Item>
        <Nav.Item>
        <Nav.Link className='nav-links'id='eeee' onClick={nav}>eeee</Nav.Link>
        </Nav.Item>
        <Nav.Item>
        <Nav.Link className='nav-links'id='rrrr' onClick={nav}>rrrr</Nav.Link>
        </Nav.Item>
        </Nav>
         </div>
        {fruits && <Fruits/>}
        {dishes && <Dishes/>}
      
    </div>
  )
}

export default App;
