import React,{useContext, useState} from 'react'
import { Card, Nav} from 'react-bootstrap'
import {Link,useHistory} from 'react-router-dom'

import 'firebase'
import './signup.css'
import {Form,Button,Carousel} from 'react-bootstrap'
import 'bootstrap/dist/js/bootstrap.bundle'
import {FirebaseContext} from './lib'
import {fire} from './configure'

function Signup() {
  const history=useHistory()
  const firebee=fire

  const [name, setname] = useState('')
  const [email, setemail] = useState('')
  const [password, setpassword] = useState('')
  const [error_msg, seterror_msg] = useState('')
  const [success, setsuccess] = useState('')
  const [verify, setverify] = useState(false)
    const K=()=>{
      return(
       
               <Card >
                   <Card.Body>
                   <Card.Title>
                    
                       Login
                   </Card.Title>
                  
                    <Form onSubmit={handleSubmit}>
                        <Form.Group controlId="formBasicname">
    <Form.Label>Name</Form.Label>
    <Form.Control type="text" placeholder="Enter name"  value={name} onChange={(e)=>setname(e.target.value)}  />
  </Form.Group>
  <Form.Group controlId="formBasicEmail">
    
    <Form.Label>Email address</Form.Label>
    <Form.Control type="email" placeholder="Enter email" value={email} onChange={(v)=>console.log(v)}  />
    {error_msg==='The email address is already in use by another account.' && <h6 className='error' id='error_email'>{error_msg}</h6>}
  </Form.Group>
    
  <Form.Group controlId="formBasicPassword">
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" placeholder="Password" value={password}  onChange={(v)=>setpassword(v.target.value)}  />
    {error_msg==='Password should be at least 6 characters' && <h6 className='error' id='error_pass'>{error_msg}</h6>}
  </Form.Group>
<Button variant="success" id='but' type="submit" >
    Get Started
  </Button>
</Form>



<Nav className='nav-mar'>
    <h6>Already an User?&nbsp; </h6>
    <a href='/signin' className='navv'>Sign in</a>
</Nav>
                  
    </Card.Body>
    </Card>
      )
    }
    let J=()=>{
      return(
        <div id="carousel" class="carousel slide carousel-fade" data-bs-ride="carousel">
  <div class="carousel-inner">
    <div class="carousel-item active" data-bs-interval="100000">
      <img src="productin1.jpg" class="d-block w-100" alt="..."/>
    </div>
    <div class="carousel-item" data-bs-interval="422000">
      <img src="productin2.jpg" class="d-block w-100" alt="..."/>
    </div>
    <div class="carousel-item" data-bs-interval="422000">
      <img src="productin3.jpg" class="d-block w-100" alt="..."/>
    </div>
  </div>
 
</div>
      )
    }
  
    const handleSubmit=(event)=>{
      event.preventDefault();
    console.log('submit')

    return firebee
      .auth()
      .createUserWithEmailAndPassword(email, password)
      .then((result) =>
        result.user
          .updateProfile({
            displayName: name,
           
          })
        
          .then(() => {
            /*var settings={
              url:'https://productin-938b4.firebaseapp.com/?email='+user,
              handleCodeInApp:false,
              dynamicLinkDomain: 'http://localhost:3000'
            }*/
            var user = fire.auth().currentUser;
            user.sendEmailVerification().then(function(){
              setsuccess('Verification link sent successfully')
              
            setverify(true)
            })
           
          })
      )
      .catch((error) => {
        if(error.message==='The email address is already in use by another account.'){
          setemail('');
        }
        if(error.message==='Password should be at least 6 characters'){
          setpassword('')
        }
        setsuccess('');
        seterror_msg(error.message);
        console.log(error_msg)
      });
      
  };


  
  
    return (
       <div id='signup_div'>
            
            <div className='signup gx-0' id='signup'>
        
         {verify && <><h2>kkk</h2><J/></>}
          {!verify && <><K/><J/></>}
               
            </div>
        </div>
    )
}

export default Signup
