import React,{useContext,useState} from 'react'
import { Card, Nav } from 'react-bootstrap'
import './sign.css'
import {Form,Button,Carousel} from 'react-bootstrap'
import $ from 'jquery'
import {Link, useHistory} from 'react-router-dom'
import 'bootstrap/dist/js/bootstrap.bundle'
import {fire} from './configure'
import firebase from 'firebase/app'

function Signin() {
  const history=useHistory()
       

     const firebee=fire
    const [success1, setsuccess1] = useState(false)
    const [fail, setfail] = useState('')
      const [email, setemail] = useState('')
      const [pass, setpass] = useState('')
      const [user, setuser] = useState(false)
      
      
      
  const googleSignIn=()=>{
    var provider = new firebase.auth.GoogleAuthProvider();
    firebase.auth().signInWithRedirect(provider);
    firebase.auth().getRedirectResult()
    .then((result)=>{
    
        setuser(true)
    
    
      history.push('/')
    
    
    
    
    
    })  
    .catch((error)=>{
      console.log(error.message)
    })
    
  }
    const handleSubmit=(event)=>{
    event.preventDefault()

  firebee.auth().signInWithEmailAndPassword(email, pass)
  .then((userCredential) => {
    // Signed in
    var user = userCredential.user;
    setsuccess1(true)
    setTimeout(function(){
      history.push('/')
    },2000);
  
    // ...
  })
  .catch((error) => {
 
    var errorMessage = error.message;
    setfail(errorMessage)

  });
}

    
      return(
      
      
    <div id='signin_div'>
           {success1 && <h4 className='text-center pt-5 '>Logged in Successfully.</h4>}
               {fail && <h4 className='text-center pt-5 '>{fail}</h4>}
            <div className='signin gx-0' id='signin'> 
              
             <Card >
                   <Card.Body>
                      
                   <Card.Title>

              
                       Login
                   </Card.Title>
                   <Nav>
                   <Link className='mink' onClick={googleSignIn}>
                       <img src='google.png' width='12%'/>
                       Sign in with Google
                  </Link>
                  </Nav>

                  <Card.Text>( Or )</Card.Text>
                    <Form onSubmit={handleSubmit}>
  <Form.Group controlId="formBasicEmail">
    <Form.Label>Email address</Form.Label>
    <Form.Control type="email" placeholder="Enter email" value={email} onChange={(v)=>setemail(v.target.value)} />
  </Form.Group>
    
  <Form.Group controlId="formBasicPassword">
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" placeholder="Password"  value={pass} onChange={(v)=>setpass(v.target.value)} />
  </Form.Group>
  <h6 id='forget'><Link to='/reset'>Forgot Password?</Link></h6>
<Button variant="primary" class='submit1' type='submit'>
 
    Submit
  </Button>

</Form>

 <Nav className='nav-mar'>
    <h6>New user???</h6>
    <a href='/signup' className='navv'>Sign up Now</a>
  </Nav>
</Card.Body>
</Card>
        

  <div id="carouselExampleFade" class="carousel slide carousel-fade" data-bs-ride="carousel">
  <div class="carousel-inner">
    <div class="carousel-item active" data-bs-interval="400000">
      <img src="productin1.jpg" class="d-block w-100" alt="..."/>
    </div>
    <div class="carousel-item" data-bs-interval="400000">
      <img src="productin2.jpg" class="d-block w-100" alt="..."/>
    </div>
    <div class="carousel-item" data-bs-interval="400000">
      <img src="productin3.jpg" class="d-block w-100" alt="..."/>
    </div>
  </div>
  
</div>  
</div>
        </div>     
    )
}

export default Signin
