import React,{useContext,useState} from 'react'
import { Card, Nav } from 'react-bootstrap'

import {Form,Button,Carousel} from 'react-bootstrap'
import {FirebaseContext} from './lib'
import './forgot.css'
function Forgot() {
         const {firebase}=useContext(FirebaseContext)
    const [success, setsuccess] = useState(false)

         const [email, setemail] = useState('')
    const HandleSubmit1=(event)=>{
    event.preventDefault();
    
  var auth=firebase.auth();
  auth.sendPasswordResetEmail(email).then(function(){
    setsuccess('Password Reset link sent successfully!')
    setemail('')
  })
}

     
            

     
  return(
    <div id='signin_div'>
           
            <div className='signin gx-0' id='signin'>
               <Card>
                   
                     <Form onSubmit={HandleSubmit1} className='form'>
                         {success && <h2>{success}</h2>}
    <h2 className='reset' >Reset Password</h2>
  <Form.Group controlId="formBasicEmail">
    <Form.Label className='email' >Email address</Form.Label>
    <Form.Control type="email" placeholder="Enter email" value={email} onChange={(e)=>setemail(e.target.value)} />

  </Form.Group>
<Button variant="primary" type="submit" className='mt-3'>
 
    Submit
  </Button>
  <div className='goto'>Return to &nbsp;<a href='/signin'>Sign in</a></div>
  
    </Form>
          </Card>         
                   <div id="carouselExampleFade" class="carousel slide carousel-fade" data-bs-ride="carousel">
  <div class="carousel-inner">
    <div class="carousel-item active" data-bs-interval="400000">
      <img src="productin1.jpg" class="d-block w-100" alt="..."/>
    </div>
    <div class="carousel-item" data-bs-interval="400000">
      <img src="productin2.jpg" class="d-block w-100" alt="..."/>
    </div>
    <div class="carousel-item" data-bs-interval="400000">
      <img src="productin3.jpg" class="d-block w-100" alt="..."/>
    </div>
  </div>
  
</div> 
      </div>
      </div>
  )
}
            

    


export default Forgot
